footer{
    margin: 150px 10px 0px 10px;
    background-color: var(--color-primary);
    padding: 3rem 0;
    text-align: center;
    border-radius: 2rem;
    font-size: 0.9rem;
    border: 4px solid black;
}
footer a{
    
    color: black;
    font-size: 25px;
}
.permalinks{
    display: inline-flex;
    gap: 8rem;
   position: relative;
   left: 42px;
}
.footer_logo{
    
    color: antiquewhite;
    position: relative;
    bottom: 50px;
    right: -390px;
    letter-spacing: 4px;
    justify-content: center;
}
.footer_socials a {
  display: inline;
  padding: 20px;
  position: relative;
  top: 3rem;
  justify-content: center;
}
.footer_copyright{
    position: relative;
    top: 2.5rem;
    justify-content: center;
}

/*--------------------MEDIA QUERIES--FOR SMALL DEVICES------------------------*/
@media screen and (max-width:600px){
  footer{
    position: relative;
    top: 900px;
    margin-left: 5px;
    
  }
  .footer_logo{
    letter-spacing: 0;
    position: absolute;
    right: 100px;
    bottom: 150px;
  }
   .permalinks{
    letter-spacing: 0px;
    gap: 8px;
    margin-left: -5.8rem;
    margin-top: 30px;
    font-size: 20px;
    font-weight: bolder;
   }
}